import React , { useState } from 'react';
import {connect} from 'react-redux';
import './portfolio.scss';

const Portfolio = (props) => {
    let [content , setContent] = useState(false);
    let [project , setProject] = useState(false);
    window.addEventListener('scroll',()=>{
        let y = window.scrollY ; 

        if(y <1300&& y > 320){
            setContent(true)
        }else{
            setContent(false)
        }
        if(y <1350&& y > 360){
            setProject(true)
        }else{
            setProject(false)
        }
    })
    return (
        <div className="container">
            <h2 className={content ? 'show header' : 'header'}>Portfolio</h2>
            <p className={content ? 'show header' : 'header'}>checkout some of my work</p>
            {props.data.map((ele, index)=>{
                return(
                <section key={ele.id} className="expma">
                <h3>{ele.title}</h3>
                <a  target="_blank" href={ele.ProjectURL}>
                    <img  className={project ? 'PW responsize':"responsize"} src={ele.imgURL} alt={ele.imgAlt}/>
                </a>
                </section>
            )})}
        </div>
    );
};
const mapStateToProps = (state)=>{
    return {
        data: state.portfolioReducer.dataPortifolio
    }
}


export default connect(mapStateToProps)(Portfolio);