import React from 'react';
import './contact.scss';
import { FontAwesomeIcon  } from '@fortawesome/react-fontawesome';
import {  } from '@fortawesome/free-brands-svg-icons'
import { faPhoneSquareAlt , faEnvelope, faBlenderPhone} from '@fortawesome/free-solid-svg-icons'
import {  } from '@fortawesome/free-regular-svg-icons'
const contact = () => {
    return (
        <div className="Container">
            <h2 className="header">contact</h2>
            <p className="header">less Contact</p>
            <section className="d-flex section-container">
                <div>
                    <FontAwesomeIcon className="red" icon={faPhoneSquareAlt}/>
                    <p>xxx-xxx-xxx</p>
                </div>
                <div>
                    <FontAwesomeIcon className="red" icon={faEnvelope}/>
                    <p>ManoErik@gmail.com</p>
                </div>
                <div>
                    <FontAwesomeIcon className="red" icon={faBlenderPhone}/>
                    <p>+100 011000000</p>
                </div>
            </section>
        </div>
    );
};



export default contact;