import React from 'react';
import './navbar.scss';
import {Link} from "react-router-dom";
const navbar = () => {
    return (
        <nav className="nav">
            <ul className="NavLinks">
                <li className="navLinksContainer">
                    <Link className="nav-items home" to="/">home</Link>
                </li>
                <li className="navLinksContainer">
                    <Link className="nav-items about" to="/about">about</Link>
                </li>
                <li className="navLinksContainer">
                    <Link className="nav-items portfolio" to="/portfolio">portfolio</Link>
                </li>
                <li className="navLinksContainer">
                    <Link className="nav-items contact" to="/contact">contact</Link>
                </li>
            </ul>
        </nav>
    );
};


export default navbar;