import React ,{useRef , useState}from 'react';
import Navbar from './../navbar/navbar';
import About from './../about/about';
import Contact from './../contact/contact';
import Portfolio from './../portfolio/portfolio'
import './home.scss';

const Home = () => {
    const {word, setWord} = useState('Mano Erik');
    const devName = useRef(null);
    const devJob = useRef(null);
    const ani = ()=>{
        devName.current.click();
    }
    const devJobBe = ()=>{
        devJob.current.classList.add('devJobTrans')
    }
    const wordSetting = ()=>{
        // setTimeout(()=>{
            let i = 0;
            let  res = '';
            const timer = setInterval(()=>{
                const name = 'Mano Erik';



                res = res + name[i] ; 
                
                
                devName.current.textContent = res;

                i++;
                if(name.length === res.length){
                    clearInterval(timer)
                    devName.current.classList.add('Dev_ani')
                    // devName.current.classList.remove('Dev_NotAni')
                }

            },200)

            

        // }, 1500)
        devName.current.click();


        // console.log(devName.current.textContent)





    }

    return (
        <div>
            <main className="main">
                <Navbar />
                <div>
                    <section className="intro">
                        <p onAnimationEnd={ani} className="time-intro">Good Morning im</p>
                        <p onAnimationEnd={devJobBe} onClick={wordSetting}  ref={devName} className="dev-name Dev_NotAni"> </p>
                        <p ref={devJob} className="dev-job">A Frontend Webdeveloper</p>
                    </section>
                </div>
            </main>
            <section className="min-main-90">
                <About />
            </section>
            <section className="min-main-90">
                <Portfolio />
            </section>
            <section className="main-75">
                <Contact />
            </section>
        </div>
    );
};



export default Home;